.profileInfo {
  position: relative;
}
.descriptionBlock{
  padding: 10px;
  display: flex;
  justify-content: start;
}
.userName {
  margin-left: 20px;
  font-size: 1.9em;
  color: #000;
}
.photoUserField {
  display: flex;
}
.photoUserField img {
  width: 200px;
  height: 200px;
  border-radius: 100%;
}
.photoUser + .changePhoto {
  display: none;
}
.photoUser:hover + .changePhoto {
  position: absolute;
  display: block;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.75);
}
.photoUserField input[type="file"] {
  display: none;
}
.aboutUser {
  margin-left: 10px;
  font-size: 1.1em;
  color: #000;
  display: flex;
  flex-direction: column;
}
.fieldStatus, .inputStatus input {
  background-color: #fff;
  height: 20px;
  width: 150px;
  border-radius: 5px;
  padding: 5px 4px;
  box-shadow: inset 0px 1px 1px 2px ghostwhite;
  outline: none;
  border: none;
}
.statusWrapped {
  overflow: hidden;
  white-space: nowrap;
}
.textStatus {
  width: max-content;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  animation: scroll-left 20s infinite 5s;
}
@keyframes scroll-left {
  0%, 30% {
    overflow: visible;
    right: 0px;
    transform: translateX(0px);
  }
  100% {
    overflow: visible;
    right: 700px;
    transform: translateX(-700px);
  }
}
.statusForm {
  margin-left: 20px;
}
.inputStatus {
  display: flex;
  flex-direction: column;
}
.error input {
  box-shadow: inset 0px 1px 1px 2px red;
}
.inputStatus span {
  background-color: red;
  margin-top: 5px;
  padding: 5px;
  border-radius: 5px;
  color: white;
}